import React from 'react';
import NavBar from '../components/navbar';
import { FaInstagram, FaGithub, FaTwitter } from "react-icons/fa";

export default class AboutPage extends React.Component{
    constructor(props){
        super(props);
    }

    render(){
        return(
            <div class="AboutPage">
                <NavBar/>
                <div className="center-align center">

                        <img src="https://i.imgur.com/lY4fWAz.png" className="circle responsive-img" style={{marginTop: 100, width: '10%', display:"inline-block"}}></img>
                        <p style={{fontSize: 30, cursor: 'pointer'}}><FaInstagram onClick={() => window.open('https://www.instagram.com/olle.thunberg/', '_blank')}/> <FaGithub onClick={() => window.open('https://github.com/ThunbergOlle', '_blank')}/> <FaTwitter onClick={() => window.open('https://twitter.com/Thunberg003', '_blank')}/></p>
                        <p style={{fontWeight:"lighter", fontSize:30, marginBottom: 0}}>Olle Thunberg</p><br/>
                        <div style={{width: "50%", margin:"auto"}}>
                        <p style={{fontWeight:"lighter", fontSize:20}}>I began playing video-games in an early age. But I am not that kind of guy that just acceptes thing for how they are. I want to know how it works because I can do it better. (I couldn't really but I thought so) So I began creating my own servers for games like Minecraft, Unturned and Rust. They didn't turn out to be very successful but this was my introduction to what's going on behind the scene of software. I also thought that making my own website would be kind of cool so I started by the age of 11 learning HTML. Soon after came css and php. There were also something weird called "JavaScript". It didn't look like the things I saw before. And I did not like it. Eventually I wanted to add more features to the website which was dependant on Javascript. So I tought, "well, lets give it a go.." and I began programming. 
                        </p><p style={{fontWeight:"lighter", fontSize:20}}>Soon after I discovered Node.js which allowed me to use my bad (at the time) Javascript skills on the backend. I made some small programms and which I later used in order to make a Steam bot. After that I learned to use C# when making small little useless games that has never been finished. One day I thought that Java would be kind of cool to learn. So I made some mods for minecraft aswell. Not because I had to, simply beause it was the best thing I knew.</p>

                        </div>
                </div>

            </div>
        )
    }
}