import React from 'react';
import 'materialize-css';
import  { BrowserRouter  } from 'react-router-dom';


export default class Card extends React.Component {
    constructor(props) {
        super(props)
        this.title = this.props.title;
        this.moreinfo = this.props.moreinfo;
        this.github = this.props.github;
        this.image = this.props.image;
        this.opengithub = () => window.open(this.props.github, '_blank');
    }

    render() {
        return (
            <div style={{ margin: "2% auto", display:"inline-block", marginLeft: 10, marginRight: 10, width: 300}} className="center-align">

<div className="card">
        <div className="card-image">
          <img src={this.image}/>
          <span className="card-title">{this.title}</span>
        </div>
        <div className="card-content">
          <p>{this.moreinfo}</p>
        </div>
        <div className="card-action">
          <a href={this.github}>View project</a>
        </div>
      </div>
            </div>

        )
    }
}