import React from 'react';
import NavBar from '../components/navbar';
import { IoMdSend } from "react-icons/io";
export default class ContactPage extends React.Component{
    constructor(props){
        super(props)
    }
    render(){
        return(
            <div>
                <NavBar/>
                <div className="row" style={{marginLeft: "20%", marginRight: "20%", marginTop: 100}}>
                    <h2 style={{fontWeight: "lighter"}}>Send me a message</h2>
                    <p>You message will be sent as an email.</p>

                <form className="col s12">
                    <div className="row">
                        <div className="input-field col s6">
                        <input id="first_name" type="text" className="validate"/>
                        <label for="first_name">First Name</label>
                        </div>
                        <div className="input-field col s6">
                        <input id="last_name" type="text" className="validate"/>
                        <label for="last_name">Last Name</label>
                        </div>
                    </div>
                    <div className="row">
                        <div className="input-field col s12">
                        <input id="email" type="email" className="validate"/>
                        <label for="email">Email</label>
                        </div>
                    </div>
                    <div className="row">
                        <div className="input-field col s12">
                        <input id="subject" type="text" className="validate"/>
                        <label for="subject">Subject</label>
                        </div>
                    </div>
                    <div className="row">
                        <div class="input-field col s12">
                        <textarea id="textarea1" class="materialize-textarea"></textarea>
                        <label for="textarea1">Content</label>
                        </div>
                    </div>
                    <button class="btn waves-effect waves-light right disabled"  style={{cursor: "not-allowed"}} type="submit" name="action">Send
                        <i class="material-icons right"><IoMdSend/></i>
                    </button>
                    </form>
                </div>
               
            </div>
        )
    }
}