import React from 'react';
import './materialize.min.css';
import M from 'materialize-css'; 
import { IoMdMenu} from 'react-icons/io'
import {NavLink} from 'react-router-dom';

function NavBar(){
  document.addEventListener('DOMContentLoaded', function() {
    var elems = document.querySelectorAll('.sidenav');
    var instances = M.Sidenav.init(elems, null);
  });
    return(
      <div>
        <nav>
        <div className="nav-wrapper black">
          <a href="/index/" className="brand-logo left white-text" style={{position: "unset", marginLeft: "5%"}}>Olle Thunberg</a>
          <a href="#" data-target="mobile-demo" class="sidenav-trigger right"><IoMdMenu style={{fontSize: 40, marginTop:12}}/></a>
          <ul id="nav-mobile" className="right hide-on-med-and-down white-text">
            <li><a href="/projects/" className="white-text">Projects</a></li>
            <li><a href="/about/" className="white-text">About</a></li>
            <li><a href="/contact/" className="white-text">Contact</a></li>
          </ul>
        </div>
      </nav>
      <ul class="sidenav black" id="mobile-demo">
          <li><a href="/projects/" className="white-text">Projects</a></li>
          <li><a href="/about/" className="white-text">About</a></li>
          <li><a href="/contact/" className="white-text">Contact</a></li>
      </ul>
      </div>

    )
}

export default NavBar;