import React from 'react';
import NavBar from '../components/navbar';
import Card from '../components/card';

export default class ProjectPage extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="projectpage">
                <NavBar />
                <div className="header center-align text-white"><h1 className="black-text" style={{ fontWeight: "lighter" }}>Projects</h1>
                    <p className="black-text">Here are some of the projects I've developed and some information about them!</p>
                </div>
                <div className="center center-align">

                    <Card title="Steam Trading Bot" github="https://github.com/ThunbergOlle/SteamTradeBot" moreinfo="This steambot was one of my first projects that I did using Node.js. This was my introduction to the Javascript world which made me interested in backend nodejs and module development." image="https://i.imgur.com/1KL3OSS.jpg" />
                    <Card title="CSGO League Bot" github="https://github.com/csgo-league/csgo-league-bot" moreinfo="This bot is one of the biggest projects I've worked on together with other developers. This gave me greater experience with advanced Javascript programming and working with a developer-team." image="https://i.imgur.com/qhy6PoT.jpg" />
                    <Card title="Armour+ Mod" github="https://github.com/ThunbergOlle/ArmourPlus" moreinfo="Armour+ is a project I made in order to get into modding with Java and modding for minecraft. Turned out to make a couple of versions before the project was abonded. It gave me experience with Java! :)" image="https://i.imgur.com/8y86q7r.jpg" />
                    <br></br>
                    <Card title="steam-price" github="https://github.com/ThunbergOlle/steam-price" moreinfo="This project is my most downloaded project out of all the npm packages I have released. It was also one of my first npm pacakge to be released to the public. It is a small and easy to use package for people to enjoy!" image="https://i.imgur.com/XQuprTs.jpg" />
                    <Card title="steam-inventory" github="https://github.com/ThunbergOlle/steam-inventory" moreinfo='This is also one of my most downloaded npm modules that I have released. It is a small and simple npm module but very useful! It was created for my SteamBot project but I later turned out not to use it.' image="https://i.imgur.com/XQuprTs.jpg"/>
                    <Card title="Admin Book plugin" github="https://github.com/ThunbergOlle/adminTools" moreinfo="Admin book was as well as Armour+ a way for me to learn more about the Java enviroment. I made an easy way to interreact with the minecraft console without having to use commands. You simply read and click." image="https://i.imgur.com/8y86q7r.jpg" />

                </div>
            </div>

        )
    }
}