import React from 'react';
import logo from './logo.svg';
import {BrowserRouter as Router, Route} from 'react-router-dom';

import MainPage from './pages/mainpage.js';
import ProjectPage from './pages/projectpage.js';
import AboutPage from './pages/aboutpage.js';
import ContactPage from './pages/contactpage.js';

import './App.css';

function App() {
  return (
    <Router>
      <Route path="/index/" render={() => {return(<MainPage/>)}}></Route>
      <Route path="/projects/" exact strict render={() => {return(<ProjectPage/>)}}></Route>
      <Route path="/about/" exact strict render={() => {return(<AboutPage/>)}}></Route>
      <Route path="/contact/" exact strict render={() => {return(<ContactPage/>)}}></Route>

    </Router>

  );
}

export default App;
